.about {
	position: relative;

	padding: 132px 0 113px;
	margin-bottom: 144px;

	@include bp(md) {
		padding: 50px 0 50px;
		margin-bottom: 50px;
	}
	
	&__breadcrumbs {
		margin-bottom: 38px;
		justify-content: flex-start;
	}

	&__info {
		max-width: 425px;

		@include bp(lt) {
			max-width: 50%;
		}

		@include bp(md) {
			max-width: 100%;
		}
	}

	&__title {
		margin-bottom: 25px;

		font-size: 30px;
		font-weight: 700;

		@include bp(sm) {
			font-size: 24px;
		}
	}

	&__text {
		margin-bottom: 57px;

		font-size: 15px;
		line-height: 2.1;
	}

	&__more-link {
		position: relative;

		padding: 11px 57px 16px 19px;

		&::after {
			position: absolute;
			right: 27px;
			top: 17px;

			width: 12px;
			height: 12px;
			
			@include bg;
			background-image: url('../images/sprite.svg#more-link-arrow');
			content: '';
		}
	}

	&__slider {
		position: absolute;
		top: 39px;
		right: 0;

		max-width: 739px;
		width: 100%;

		@include bp(lt) {
			position: relative;
		}
	}

	&__slider-wrapper {
		line-height: 0;
	}

	&__slide-img {
		@include bp(lg) {
			width: 100%;
			height: auto;
		}
	}

	&__slider-nav {
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 1;

		display: flex;
	}

	&__slider-btn {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 67px;
		width: 67px;

		background-color: $slider-nav-color;
		cursor: pointer;
		transition: background-color 0.3s;

		&:hover {
			background-color: $white;
		}

		&:hover::after {
			background-color: $black;
		}

		&::after {
			width: 12px;
			height: 12px;

			@include mask-bg;
			mask-image: url('../images/sprite.svg#slider-arrow');
			background-color: $white;

			content: '';
			transition: background-color 0.3s;
		}

		&--next {
			left: 117px;
			transform: rotate(180deg);

			&::after {
				transform: translateY(-25%);
			}
		}

		&.swiper-button-disabled {
			background-color: #2f2f2f;

			cursor: default;

			&::after {
				background-color: rgba($white, 0.3);
			}
		}
	}

	&__slider-fraction {
		padding: 17px 0;

		background-color: $slider-nav-color;

		font-weight: 500;
		line-height: 2.05;
	}
}