html,
body {
	min-height: 100%;
}

body {
	background-color: $black;

	font-family: 'Poppins';
	font-size: 16px;
	font-weight: 400;
	color: $white;
	line-height: normal;
}

.index-bg {
	background: linear-gradient(180deg, #000 0%, #4A2800 100%);
	backdrop-filter: blur(7px);
}

.about-bg {
	background: linear-gradient(180deg, #010101 -15.71%, #1A0E01 99.57%);
}

.portfolio-bg {
	background: linear-gradient(177deg, #050301 -30.15%, #120A01 92.85%);
}

.overflow {
	overflow: hidden;
}

.overlay {
	position: fixed;
	z-index: 1;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	background-color: rgba($black, 0.7);
	opacity: 0;
	visibility: hidden;
	
	transition: opacity 0.3s, visibility 0.3s;

	&--active {
		opacity: 1;
		visibility: visible;
	}
}

.wrapper {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	width: 100%;
	max-width: 1200px;
}