.overview {
	padding-top: 114px;
	margin-bottom: 180px;

	&__subtext {
		margin-bottom: 45px;

		text-align: center;
	}

	&__info {
		display: flex;
		justify-content: center;
		padding: 37px 15px;
		max-width: 1140px;
		margin: 0 auto 132px;

		background-color: $dark-el-bg;
		border-radius: 10px;
	}

	&__info-item {
		padding: 30px 0 30px 91px;

		background-repeat: no-repeat;
		background-size: 55px;
		background-position: left center;

		font-family: 'Rubik';

		&:not(:last-child) {
			padding-right: 118px;
			margin-right: 113px;
			border-right: 2px solid #EEEFF2;
		}

		&--clients {
			background-image: url('../images/sprite.svg#overview-user');
		}

		&--countries {
			background-image: url('../images/sprite.svg#overview-location');
		}

		&--projects {
			background-image: url('../images/sprite.svg#overview-server');
		}
	}

	&__info-quantity {
		display: block;
		margin-bottom: 5px;

		font-size: 25px;
		font-weight: 700;
	}

	&__info-lower {
		font-size: 20px;
	}
	
	&__wrapper {
		display: flex;
		justify-content: space-between;
		column-gap: 25px;
	}

	&__video {
		max-width: 669px;
		width: 100%;
		height: 355px;

		border-radius: 16px;
	}

	&__group {
		max-width: 380px;
	}

	&__subtitle {
		margin-bottom: 12px;

		font-size: 30px;
		font-weight: 700;
		line-height: 1.8;
	}

	&__text {
		margin-bottom: 28px;

		font-size: 14px;
		line-height: 1.8;
	}

	&__list {
		list-style: none;
	}

	&__list-item {
		position: relative;
		padding-left: 34px;

		font-size: 14px;
		line-height: 1.8;

		&:not(:last-child) {
			margin-bottom: 19px;
		}

		&::before {
			position: absolute;
			left: 0;

			width: 24px;
			height: 24px;

			@include bg;
			background-image: url('../images/sprite.svg#overview-check');

			content: '';
		}
	}
}