.footer {
	background-color: $black;

	&__container {
		display: flex;
		justify-content: space-between;
		padding: 73px 15px 53px;

		font-size: 16px;

		@include bp(lg) {
			gap: 30px;
			justify-content: space-around;
			flex-wrap: wrap;
		}

		@include bp(md) {
			justify-content: space-between;
		}

		@include bp(sm) {
			justify-content: flex-start;
		}
	}

	&__column {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		max-width: 280px;
	}

	&__logo {
		margin-bottom: 15px;
	}

	&__subtitle {
		margin-bottom: 15px;

		font-size: 17px;
		font-weight: 500;
	}

	&__text {
		margin-bottom: auto;
	}

	&__list {
		list-style: none;
	}

	&__item {
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__link {
		transition: opacity 0.3s;

		&:hover {
			opacity: 0.6;
		}
	}

	&__social {
		align-self: flex-end;
	}

	&__copy {
		padding: 29px 0;
		border-top: 1px solid $footer-line-color;

		text-align: center;
		font-size: 13px;
	}
}