.more-btn {
	display: inline-block;
	padding: 10px 30px;
	border: 0;

	border-radius: 10px;
	background-color: rgba($white, 0.1);
	cursor: pointer;

	font-family: 'Rubik';
	line-height: 1.9;
	font-size: 20px;
	text-transform: uppercase;
	font-weight: 500;

	transition: background-color 0.3s, color 0.3s;

	&:hover {
		background-color: $white;

		color: $black;
	}
}