.team {
	margin-bottom: 153px;

	@include bp(md) {
		margin-bottom: 70px;
	}

	&__title {
		margin-bottom: 53px;

		font-size: 25px;
		font-weight: 600;
		line-height: 1.8;
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
		place-items: center;
		gap: 28px;
		margin-bottom: 157px;

		list-style: none;

		@include bp(md) {
			margin-bottom: 70px;
		}
	}

	&__item {
		position: relative;

		max-width: 295px;
		width: 100%;
		padding: 155px 15px 30px 15px;

		background-color: $dark-el-bg;
		border-radius: 20px;

		text-align: center;

		&::after {
			position: absolute;
			top: 35px;
			left: calc(50% - 60px);

			width: 120px;
			height: 120px;

			@include bg;
			background-size: contain;

			content: '';
		}

		&--first::after {
			background-image: url('../images/team/team-1.png');
		}

		&--second::after {
			background-image: url('../images/team/team-2.png');
		}

		&--third::after {
			background-image: url('../images/team/team-3.png');
		}

		&--fourth::after {
			background-image: url('../images/team/team-4.png');
		}
	}

	&__name {
		font-size: 19px;
		font-weight: 600;
		line-height: 2;
	}

	&__role {
		font-weight: 300;
		line-height: 2;
	}
}