.social {
	display: flex;

	list-style: none;

	&__item {
		&:not(:last-child) {
			margin-right: 16px;
		}
	}

	&__link {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 34px;
		height: 34px;

		border-radius: 50%;
		background-color: $white;
		
		&:hover {
			background-color: $orange;
		}

		&:hover .social__link-icon {
			background-color: $white;
		}
	}

	&__link-icon{
		width: 13px;
		height: 13px;

		@include mask-bg;
		background-color: $black;
	}

	&__link,
	&__link-icon {
		transition: background-color 0.3s;
	}
}