@import '../../node_modules/swiper/swiper.css';
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li {
  margin: 0;
}

ul,
ol {
  padding: 0;
}

input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Poppins-Light.woff2"), url("../fonts/Poppins-Light.woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Poppins-Regular.woff2"), url("../fonts/Poppins-Regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Poppins-Medium.woff2"), url("../fonts/Poppins-Medium.woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Poppins-SemiBold.woff2"), url("../fonts/Poppins-SemiBold.woff");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Poppins-Bold.woff2"), url("../fonts/Poppins-Bold.woff");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Rubik-Medium.woff2"), url("../fonts/Rubik-Medium.woff");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Rubik-Bold.woff2"), url("../fonts/Rubik-Bold.woff");
  font-display: swap;
}
html,
body {
  min-height: 100%;
}

body {
  background-color: #000;
  font-family: "Poppins";
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  line-height: normal;
}

.index-bg {
  background: linear-gradient(180deg, #000 0%, #4A2800 100%);
  backdrop-filter: blur(7px);
}

.about-bg {
  background: linear-gradient(180deg, #010101 -15.71%, #1A0E01 99.57%);
}

.portfolio-bg {
  background: linear-gradient(177deg, #050301 -30.15%, #120A01 92.85%);
}

.overflow {
  overflow: hidden;
}

.overlay {
  position: fixed;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}
.overlay--active {
  opacity: 1;
  visibility: visible;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1200px;
}

.menu {
  display: flex;
  list-style: none;
}
@media only screen and (max-width: 992px) {
  .menu {
    position: fixed;
    left: -15px;
    z-index: 3;
    top: 0;
    bottom: 0;
    display: block;
    width: 280px;
    padding: 50px 0 0 40px;
    transform: translateX(-100%);
    background-color: #1C140F;
    transition: transform 0.3s ease-out;
  }
}
.menu--active {
  transform: translateX(0);
}
.menu__item:not(:last-child) {
  margin-right: 88px;
}
@media only screen and (max-width: 992px) {
  .menu__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}
.menu__item:nth-last-child(2) {
  margin-right: 36px;
}
.menu__link {
  position: relative;
  font-family: "Poppins";
  font-weight: 400;
  font-size: 16px;
  transition: opacity 0.3s;
}
.menu__link:hover {
  opacity: 0.6;
}
.menu__link--active::after {
  position: absolute;
  bottom: -11px;
  left: -12px;
  width: 39px;
  height: 4px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../images/sprite.svg#underline");
  content: "";
}
.menu__link--contact {
  padding: 10px 28px;
  font-size: 13px;
  background-color: #FA9021;
  border-radius: 6px;
  transition: color 0.3s, background-color 0.3s;
}
.menu__link--contact::after {
  display: none;
}
.menu__link--contact:hover {
  opacity: 1;
  background-color: #fff;
  color: #000;
}

.header {
  position: relative;
  padding-top: 37px;
}
.header__container {
  max-width: 1330px;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__burger {
  position: relative;
  z-index: 4;
  display: none;
  padding-right: 35px;
  border: 0;
  background-color: transparent;
  text-transform: uppercase;
  font-weight: 500;
}
@media only screen and (max-width: 992px) {
  .header__burger {
    display: block;
  }
}
.header__burger::before, .header__burger::after {
  position: absolute;
  right: 0;
  width: 30px;
  height: 2px;
  border-radius: 3px;
  background-color: #fff;
  content: "";
  transition: transform 0.3s;
}
.header__burger::before {
  top: 9px;
}
.header__burger::after {
  top: 15px;
  width: 20px;
}
.header__burger--active::before {
  top: 13px;
  width: 20px;
  transform: rotate(-45deg);
}
.header__burger--active::after {
  top: 13px;
  transform: rotate(45deg);
}

.footer {
  background-color: #000;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  padding: 73px 15px 53px;
  font-size: 16px;
}
@media only screen and (max-width: 992px) {
  .footer__container {
    gap: 30px;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 768px) {
  .footer__container {
    justify-content: space-between;
  }
}
@media only screen and (max-width: 560px) {
  .footer__container {
    justify-content: flex-start;
  }
}
.footer__column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 280px;
}
.footer__logo {
  margin-bottom: 15px;
}
.footer__subtitle {
  margin-bottom: 15px;
  font-size: 17px;
  font-weight: 500;
}
.footer__text {
  margin-bottom: auto;
}
.footer__list {
  list-style: none;
}
.footer__item:not(:last-child) {
  margin-bottom: 10px;
}
.footer__link {
  transition: opacity 0.3s;
}
.footer__link:hover {
  opacity: 0.6;
}
.footer__social {
  align-self: flex-end;
}
.footer__copy {
  padding: 29px 0;
  border-top: 1px solid #D2D2D2;
  text-align: center;
  font-size: 13px;
}

.social {
  display: flex;
  list-style: none;
}
.social__item:not(:last-child) {
  margin-right: 16px;
}
.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: #fff;
}
.social__link:hover {
  background-color: #FA9021;
}
.social__link:hover .social__link-icon {
  background-color: #fff;
}
.social__link-icon {
  width: 13px;
  height: 13px;
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
  background-color: #000;
}
.social__link, .social__link-icon {
  transition: background-color 0.3s;
}

.title {
  margin-bottom: 13px;
  font-size: 33px;
  font-weight: 600;
  text-align: center;
}

.subtitle {
  margin-bottom: 32px;
  font-size: 30px;
  font-weight: 600;
}

.subscribe__title {
  margin-bottom: 32px;
  font-size: 30px;
  line-height: 1.9;
  font-weight: 600;
}
@media only screen and (max-width: 768px) {
  .subscribe__title {
    margin-bottom: 20px;
    font-size: 26px;
  }
}
.subscribe__text {
  max-width: 545px;
  margin-bottom: 74px;
  line-height: 1.9;
}
@media only screen and (max-width: 768px) {
  .subscribe__text {
    margin-bottom: 30px;
  }
}
.subscribe__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 50px;
  border-radius: 15px;
  background-color: #1C140F;
}
@media only screen and (max-width: 768px) {
  .subscribe__wrapper {
    flex-wrap: wrap;
    row-gap: 20px;
  }
}
@media only screen and (max-width: 560px) {
  .subscribe__wrapper {
    padding: 20px 15px;
  }
}
.subscribe__info {
  max-width: 550px;
}
.subscribe__subtitle {
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 500;
}
@media only screen and (max-width: 768px) {
  .subscribe__subtitle {
    font-size: 26px;
  }
}
.subscribe__subtext {
  padding-right: 15px;
  font-size: 17px;
}
.subscribe__form {
  position: relative;
  max-width: 385px;
  width: 100%;
}
@media only screen and (max-width: 768px) {
  .subscribe__form {
    margin: 0 auto;
  }
}
.subscribe__input {
  padding: 14px 130px 14px 20px;
  width: 100%;
  border: 0;
  border-radius: 8px;
  background-color: #fff;
  font-size: 13px;
  line-height: 1.9;
  color: #000;
}
.subscribe__input::placeholder {
  font-weight: 300;
  color: #898989;
}
.subscribe__button {
  position: absolute;
  right: 6px;
  top: 6px;
  padding: 8px 20px;
  border: 0;
  border-radius: 8px;
  background-color: #DC7000;
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s;
}
@media only screen and (max-width: 560px) {
  .subscribe__button {
    padding: 8px 10px;
  }
}
.subscribe__button:hover {
  background-color: #fff;
  color: #000;
}

.breadcrumbs {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
  list-style: none;
}
.breadcrumbs__item:not(:last-child) {
  position: relative;
  margin-right: 5px;
  padding-right: 15px;
}
.breadcrumbs__item:not(:last-child)::after {
  position: absolute;
  right: 0;
  bottom: 2px;
  font-size: 12px;
  content: ">";
}
.breadcrumbs__item:last-child {
  color: #E47500;
}
.breadcrumbs__link {
  font-size: 12px;
  line-height: 2;
}

.more-btn {
  display: inline-block;
  padding: 10px 30px;
  border: 0;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
  font-family: "Rubik";
  line-height: 1.9;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 500;
  transition: background-color 0.3s, color 0.3s;
}
.more-btn:hover {
  background-color: #fff;
  color: #000;
}

.more-link {
  display: inline-block;
  background: linear-gradient(113deg, #FA8305 0%, #FB9E3C 100%);
  border-radius: 40px;
  font-size: 16px;
  font-weight: 500;
}

.intro {
  margin-top: -66px;
}
@media only screen and (max-width: 992px) {
  .intro {
    margin-bottom: 50px;
  }
}
.intro__container {
  background-repeat: no-repeat;
  background-position: calc(100% + 140px) top;
}
@media only screen and (max-width: 992px) {
  .intro__container {
    background-size: contain;
  }
}
@media only screen and (max-width: 768px) {
  .intro__container {
    background-position: top center;
  }
}
.intro__inner {
  padding: 200px 0 180px;
  max-width: 480px;
}
@media only screen and (max-width: 992px) {
  .intro__inner {
    max-width: 100%;
    padding: 120px 0 100px;
    box-shadow: inset 0 0 0 350px rgba(0, 0, 0, 0.7);
  }
}
@media only screen and (max-width: 768px) {
  .intro__inner {
    padding: 120px 0 50px;
  }
}
.intro__title {
  margin-bottom: 17px;
  font-size: 20px;
  font-weight: 700;
  color: #E87D0E;
}
.intro__lead-text {
  margin-bottom: 26px;
  font-size: 50px;
  font-weight: 700;
}
@media only screen and (max-width: 768px) {
  .intro__lead-text {
    font-size: 30px;
  }
}
.intro__text {
  margin-bottom: 64px;
  line-height: 1.9;
}
.intro__more-link {
  padding: 15px 31px;
}

.trends {
  margin-bottom: 157px;
}
@media only screen and (max-width: 768px) {
  .trends {
    margin-bottom: 50px;
  }
}
.trends__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 57px;
}
@media only screen and (max-width: 560px) {
  .trends__head {
    column-gap: 20px;
  }
}
.trends__head-title {
  font-family: "Rubik";
  font-size: 31px;
  font-weight: 700;
}
@media only screen and (max-width: 768px) {
  .trends__head-title {
    font-size: 24px;
  }
}
@media only screen and (max-width: 560px) {
  .trends__more-btn {
    flex-shrink: 0;
  }
}
.trends__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(251px, 1fr));
  gap: 47px;
  margin-bottom: 129px;
  list-style: none;
}
@media only screen and (max-width: 768px) {
  .trends__list {
    margin-bottom: 70px;
  }
}
.trends__card--hidden {
  display: none;
}
.trends__card-link {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.trends__card-img {
  margin-bottom: 25px;
  border-radius: 20px;
  transition: transform 0.3s;
}
.trends__card-img:hover {
  transform: scale(1.1);
}
.trends__card-quantity {
  position: relative;
  padding-left: 31px;
  font-family: "Rubik";
  font-size: 22px;
  font-weight: 500;
}
.trends__card-quantity::before {
  position: absolute;
  left: 0;
  width: 25px;
  height: 25px;
  mask-image: url("../images/sprite.svg#fire-icon");
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
  background-color: #fff;
  content: "";
}
.trends__title {
  max-width: 825px;
  margin: 0 auto 67px;
  font-size: 35px;
  font-weight: 600;
  text-align: center;
}
@media only screen and (max-width: 768px) {
  .trends__title {
    margin: 0 auto 40px;
    font-size: 26px;
  }
}
@media only screen and (max-width: 768px) {
  .trends__subtitle {
    font-size: 24px;
  }
}
.trends__text {
  max-width: 545px;
  margin-bottom: 46px;
}
.trends__img-container {
  position: relative;
}
.trends__img-container::before {
  position: absolute;
  top: -13%;
  right: 125px;
  z-index: -1;
  width: 196px;
  height: 154px;
  background-image: url("../images/brown-dots.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
}
.trends__img {
  width: 100%;
  border-radius: 5px;
}
@media only screen and (max-width: 992px) {
  .trends__img {
    height: auto;
  }
}

.services {
  position: relative;
  z-index: 0;
  padding: 93px 0 85px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.services::before {
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: 0;
  right: 0;
  z-index: -1;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0.65) 100%), rgba(0, 0, 0, 0.54);
  backdrop-filter: blur(2px);
  content: "";
}
.services__title {
  margin: 0 auto;
  max-width: 825px;
  margin-bottom: 25px;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}
@media only screen and (max-width: 768px) {
  .services__title {
    font-size: 26px;
  }
}
.services__text {
  margin: 0 auto 100px;
  max-width: 820px;
  line-height: 1.9;
  text-align: center;
}
.services__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 62px;
  column-gap: 100px;
  list-style: none;
}
.services__item {
  flex-shrink: 0;
  transition: transform 0.3s;
}
.services__item:hover {
  transform: scale(1.2);
}
.services__link {
  position: relative;
  display: inline-block;
  padding: 95px 0 45px;
  font-weight: 500;
}
.services__link::before, .services__link::after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  content: "";
}
.services__link::before {
  top: 0;
  width: 65px;
  height: 65px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.services__link--mobile::before {
  background-image: url("../images/services/services-1.svg");
}
.services__link--pc::before {
  background-image: url("../images/services/services-2.svg");
}
.services__link--ps::before {
  background-image: url("../images/services/services-3.svg");
}
.services__link--vr::before {
  background-image: url("../images/services/services-4.svg");
}
.services__link--design::before {
  background-image: url("../images/services/services-5.svg");
}
.services__link--3d::before {
  background-image: url("../images/services/services-6.svg");
}
.services__link::after {
  bottom: 0;
  width: 21px;
  height: 21px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../images/sprite.svg#services-arrow");
}

.projects {
  padding: 148px 0 163px;
  background: linear-gradient(152deg, #090300 0%, rgba(9, 3, 0, 0) 100%);
}
@media only screen and (max-width: 768px) {
  .projects {
    padding: 70px 0 70px;
  }
}
.projects__title {
  margin-bottom: 15px;
  font-size: 30px;
  font-weight: 700;
  line-height: 1.9;
  text-align: center;
}
@media only screen and (max-width: 560px) {
  .projects__title {
    font-size: 26px;
  }
}
.projects__text {
  margin-bottom: 71px;
  font-size: 14px;
  text-align: center;
}
.projects__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 33px 27px;
  margin-bottom: 72px;
  list-style: none;
}
.projects__item {
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 223px;
  overflow: hidden;
  border-radius: 20px;
}
.projects__item:hover .projects__img {
  transform: scale(1.2);
}
.projects__item:nth-child(1) .projects__img {
