}
.projects__item:nth-child(4) .projects__img {
  max-width: 246px;
}
.projects__item:nth-child(5) .projects__img {
  max-width: 401px;
}
.projects__item:nth-child(6) .projects__img {
  max-width: 469px;
}
.projects__img {
  transition: transform 0.3s;
}
.projects__more-btn {
  position: relative;
  left: 50%;
  margin-bottom: 105px;
  transform: translateX(-50%);
}
@media only screen and (max-width: 768px) {
  .projects__more-btn {
    margin-bottom: 50px;
  }
}

.about {
  position: relative;
  padding: 132px 0 113px;
  margin-bottom: 144px;
}
@media only screen and (max-width: 768px) {
  .about {
    padding: 50px 0 50px;
    margin-bottom: 50px;
  }
}
.about__breadcrumbs {
  margin-bottom: 38px;
  justify-content: flex-start;
}
.about__info {
  max-width: 425px;
}
@media only screen and (max-width: 1200px) {
  .about__info {
    max-width: 50%;
  }
}
@media only screen and (max-width: 768px) {
  .about__info {
    max-width: 100%;
  }
}
.about__title {
  margin-bottom: 25px;
  font-size: 30px;
  font-weight: 700;
}
@media only screen and (max-width: 560px) {
  .about__title {
    font-size: 24px;
  }
}
.about__text {
  margin-bottom: 57px;
  font-size: 15px;
  line-height: 2.1;
}
.about__more-link {
  position: relative;
  padding: 11px 57px 16px 19px;
}
.about__more-link::after {
  position: absolute;
  right: 27px;
  top: 17px;
  width: 12px;
  height: 12px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../images/sprite.svg#more-link-arrow");
  content: "";
}
.about__slider {
  position: absolute;
  top: 39px;
  right: 0;
  max-width: 739px;
  width: 100%;
}
@media only screen and (max-width: 1200px) {
  .about__slider {
    position: relative;
  }
