.header {
	position: relative;
	
	padding-top: 37px;

	&__container {
		max-width: 1330px;
	}

	&__nav {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__burger {
		position: relative;
		z-index: 4;

		display: none;
		padding-right: 35px;
		border: 0;

		background-color: transparent;

		text-transform: uppercase;
		font-weight: 500;

		@include bp(lg) {
			display: block;
		}

		&::before,
		&::after {
			position: absolute;
			right: 0;

			width: 30px;
			height: 2px;

			border-radius: 3px;
			background-color: $white;

			content: '';
			transition: transform 0.3s;
		}

		&::before {
			top: 9px;
		}

		&::after {
			top: 15px;

			width: 20px;
		}

		&--active::before {
			top: 13px;

			width: 20px;
			transform: rotate(-45deg);
		}

		&--active::after {
			top: 13px;

			transform: rotate(45deg);
		}
	}
}