.articles {
	margin-bottom: 168px;

	&__list {
		list-style: none;
	}

	&__item {
		padding: 7px 0 18px;

		background-repeat: no-repeat;
		background-position: right;
		background-size: contain;

		&:not(:last-child) {
			margin-bottom: 180px;
		}

		&:nth-child(even) {
			background-position: left;

			& .articles__inner {
				margin-left: auto;
			}
		}
	}

	&__inner {
		max-width: 525px;
	}

	&__title {
		margin-bottom: 43px;

		font-size: 30px;
		font-weight: 500;
	}

	&__text {
		font-size: 17px;

		&:not(:last-child) {
			margin-bottom: 18px;
		}

		& + .articles__text {
			margin-bottom: 46px;
		}
	}

	&__link {
		padding: 13px 44px;

		background-color: $subscribe-btn-color;
	}
}