.subscribe {
	&__title {
		margin-bottom: 32px;

		font-size: 30px;
		line-height: 1.9;
		font-weight: 600;

		@include bp(md) {
			margin-bottom: 20px;

			font-size: 26px;
		}
	}

	&__text {
		max-width: 545px;
		margin-bottom: 74px;

		line-height: 1.9;

		@include bp(md) {
			margin-bottom: 30px;
		}
	}

	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 40px 50px;

		border-radius: 15px;
		background-color: $subscribe-bg;

		@include bp(md) {
			flex-wrap: wrap;
			row-gap: 20px;
		}

		@include bp(sm) {
			padding: 20px 15px;
		}
	}

	&__info {
		max-width: 550px;
	}

	&__subtitle {
		margin-bottom: 10px;

		font-size: 30px;
		font-weight: 500;

		@include bp(md) {
			font-size: 26px;
		}
	}

	&__subtext {
		padding-right: 15px;

		font-size: 17px;
	}

	&__form {
		position: relative;
		max-width: 385px;
		width: 100%;

		@include bp(md) {
			margin: 0 auto;
		}
	}

	&__input {
		padding: 14px 130px 14px 20px;
		width: 100%;
		border: 0;

		border-radius: 8px;
		background-color: $white;

		font-size: 13px;
		line-height: 1.9;
		color: $black;
		
		&::placeholder {
			font-weight: 300;
			color: $subscribe-placeholder;
		}
	}

	&__button {
		position: absolute;
		right: 6px;
		top: 6px;

		padding: 8px 20px;
		border: 0;

		border-radius: 8px;
		background-color: $subscribe-btn-color;
		cursor: pointer;

		transition: color 0.3s, background-color 0.3s;

		@include bp(sm) {
			padding: 8px 10px;
		}

		&:hover {
			background-color: $white;

			color: $black;
		}
	}
}