html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li {
	margin: 0;
}

ul,
ol {
	padding: 0;
}

input,
button,
textarea,
select {
	font: inherit;
	color: inherit;
}

a {
	text-decoration: none;
	color: inherit;
}