.trends {
	margin-bottom: 157px;

	@include bp(md) {
		margin-bottom: 50px;
	}

	&__head {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 57px;

		@include bp(sm) {
			column-gap: 20px;
		}
	}

	&__head-title {
		font-family: 'Rubik';
		font-size: 31px;
		font-weight: 700;
		
		@include bp(md) {
			font-size: 24px;
		}
	}

	&__more-btn {
		@include bp(sm) {
			flex-shrink: 0;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(251px, 1fr));
		gap: 47px;
		margin-bottom: 129px;

		list-style: none;

		@include bp(md) {
			margin-bottom: 70px;
		}
	}

	&__card {
		&--hidden {
			display: none;
		}
	}

	&__card-link {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__card-img {
		margin-bottom: 25px;

		border-radius: 20px;

		transition: transform 0.3s;

		&:hover {
			transform: scale(1.1);
		}
	}

	&__card-quantity {
		position: relative;

		padding-left: 31px;

		font-family: 'Rubik';
		font-size: 22px;
		font-weight: 500;

		&::before {
			position: absolute;
			left: 0;

			width: 25px;
			height: 25px;

			mask-image: url('../images/sprite.svg#fire-icon');
			@include mask-bg;
			background-color: $white;

			content: '';
		}
	}

	&__title {
		max-width: 825px;
		margin: 0 auto 67px;

		font-size: 35px;
		font-weight: 600;
		text-align: center;

		@include bp(md) {
			margin: 0 auto 40px;

			font-size: 26px;
		}
	}

	&__subtitle {
		@include bp(md) {
			font-size: 24px;
		}
	}

	&__text {
		max-width: 545px;
		margin-bottom: 46px;
	}

	&__img-container {
		position: relative;

		&::before {
			position: absolute;
			top: -13%;
			right: 125px;
			z-index: -1;

			width: 196px;
			height: 154px;

			background-image: url('../images/brown-dots.svg');
			@include bg;

			content: '';
		}
	}

	&__img {
		width: 100%;

		border-radius: 5px;

		@include bp(lg) {
			height: auto;
		}
	}
}