.benefits {
	margin-bottom: 148px;

	@include bp(md) {
		margin-bottom: 70px;
	}

	&__title {
		margin-bottom: 63px;

		font-size: 25px;
		font-weight: 600;

		@include bp(md) {
			margin-bottom: 40px;
		}
	}

	&__list {
		display: grid;
		gap: 43px;
		grid-template-columns: repeat(3, 1fr);
		margin-bottom: 147px;

		list-style: none;

		@include bp(lg) {
			grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
			gap: 15px;
			justify-items: center;
			margin-bottom: 70px;
		}
	}

	&__item {
		max-width: 378px;
		padding: 64px 37px;

		background-color: $dark-el-bg;
		border-radius: 20px;
		box-shadow: 0px 4px 94px 0px rgba(0, 0, 0, 0.15);

		line-height: 1.9;

		@include bp(lg) {
			padding: 64px 15px;
		}
	}

	&__item-head {
		display: inline-block;
		padding: 7px 35px 6px 14px;
		margin-bottom: 19px;

		border-radius: 8px;

		font-size: 14px;

		&--purple {
			background: rgba(176, 0, 220, 0.11);
		}

		&--red {
			background: rgba(220, 66, 0, 0.11);
		}

		&--green {
			background: rgba(0, 220, 141, 0.11);
		}
	}

	&__item-title {
		margin-bottom: 16px;

		font-size: 20px;
		font-weight: 600;
	}

	&__item-text {
		font-size: 14px;
	}

	&__info {
		padding: 134px 0 100px;

		background-color: $dark-el-bg;
		background-repeat: no-repeat;
		background-size: 490px;
		background-position: calc(50% - 340px) 100%;

		@include bp(md) {
			padding: 50px 0 30px;
		}

		@include bp(md) {
			background-position: left bottom;
		}
	}

	&__wrapper {
		max-width: 530px;
		margin-left: auto;

		@include bp(lg) {
			padding: 15px;

			background-color: rgba($dark-el-bg, 0.8);
			border-radius: 8px;
		}
	}

	&__span {
		display: inline-block;
		margin-bottom: 14px;

		font-weight: 500;
		line-height: 2;
	}

	&__info-title {
		margin-bottom: 14px;

		font-size: 28px;
		font-weight: 600;
		line-height: 1.6;

		@include bp(sm) {
			font-size: 24px;
		}
	}

	&__text {
		line-height: 2;
	}
}