.projects {
	padding: 148px 0 163px;

	background: linear-gradient(152deg, #090300 0%, rgba(9, 3, 0, 0.00) 100%);

	@include bp(md) {
		padding: 70px 0 70px;
	}

	&__title {
		margin-bottom: 15px;

		font-size: 30px;
		font-weight: 700;
		line-height: 1.9;
		text-align: center;

		@include bp(sm) {
			font-size: 26px;
		}
	}

	&__text {
		margin-bottom: 71px;

		font-size: 14px;
		text-align: center;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 33px 27px;
		margin-bottom: 72px;

		list-style: none;
	}

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		max-height: 223px;

		overflow: hidden;
		border-radius: 20px;

		&:hover .projects__img {
			transform: scale(1.2);
		}

		&:nth-child(1) .projects__img {
			max-width: 382px;
		}

		&:nth-child(2) .projects__img {
			max-width: 382px;
		}

		&:nth-child(3) .projects__img {
			max-width: 352px;
		}

		&:nth-child(4) .projects__img {
			max-width: 246px;
		}

		&:nth-child(5) .projects__img {
			max-width: 401px;
		}

		&:nth-child(6) .projects__img {
			max-width: 469px;
		}
	}
	
	&__img {
		transition: transform 0.3s;
	}

	&__more-btn {
		position: relative;
		left: 50%;

		margin-bottom: 105px;
		transform: translateX(-50%);

		@include bp(md) {
			margin-bottom: 50px;
		}
	}
}