.services {
	position: relative;
	z-index: 0;

	padding: 93px 0 85px;

	@include bg;

	&::before {
		position: absolute;
		top: -1px;
		bottom: -1px;
		left: 0;
		right: 0;
		z-index: -1;

		background: linear-gradient(0deg, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0.65) 100%), rgba(0, 0, 0, 0.54);
		backdrop-filter: blur(2px);

		content: '';
	}

	&__title {
		margin: 0 auto;
		max-width: 825px;
		margin-bottom: 25px;

		font-size: 30px;
		font-weight: 700;
		text-align: center;

		@include bp(md) {
			font-size: 26px;
		}
	}

	&__text {
		margin: 0 auto 100px;
		max-width: 820px;

		line-height: 1.9;
		text-align: center;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		row-gap: 62px;
		column-gap: 100px;

		list-style: none;
	}

	&__item {
		flex-shrink: 0;

		transition: transform 0.3s;

		&:hover {
			transform: scale(1.2);
		}
	}

	&__link {
		position: relative;

		display: inline-block;
		padding: 95px 0 45px;

		font-weight: 500;

		&::before,
		&::after {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			content: '';
		}

		&::before {
			top: 0;

			width: 65px;
			height: 65px;

			@include bg;
		}

		&--mobile::before {
			background-image: url('../images/services/services-1.svg');
		}

		&--pc::before {
			background-image: url('../images/services/services-2.svg');
		}

		&--ps::before {
			background-image: url('../images/services/services-3.svg');
		}

		&--vr::before {
			background-image: url('../images/services/services-4.svg');
		}

		&--design::before {
			background-image: url('../images/services/services-5.svg');
		}

		&--3d::before {
			background-image: url('../images/services/services-6.svg');
		}

		&::after {
			bottom: 0;

			width: 21px;
			height: 21px;

			@include bg;
			background-image: url('../images/sprite.svg#services-arrow');
		}
	}
}