.intro {
	margin-top: -66px;

	@include bp(lg) {
		margin-bottom: 50px;
	}

	&__container {
		background-repeat: no-repeat;
		background-position: calc(100% + 140px) top;

		@include bp(lg) {
			background-size: contain;
		}

		@include bp(md) {
			background-position: top center;
		}
	}

	&__inner {
		padding: 200px 0 180px;
		max-width: 480px;

		@include bp(lg) {
			max-width: 100%;
			padding: 120px 0 100px;

			box-shadow: inset 0 0 0 350px rgba($black, 0.7);
		}

		@include bp(md) {
			padding: 120px 0 50px;
		}
	}

	&__title {
		margin-bottom: 17px;

		font-size: 20px;
		font-weight: 700;
		color: $title-color;
	}

	&__lead-text {
		margin-bottom: 26px;

		font-size: 50px;
		font-weight: 700;

		@include bp(md) {
			font-size: 30px;
		}
	}

	&__text {
		margin-bottom: 64px;
		line-height: 1.9;
	}

	&__more-link {
		padding: 15px 31px;
	}
}