.menu {
	display: flex;

	list-style: none;

	@include bp(lg) {
		position: fixed;
		left: -15px;
		z-index: 3;

		top: 0;
		bottom: 0;
		
		display: block;
		width: 280px;
		padding: 50px 0 0 40px;
		transform: translateX(-100%);

		background-color: $subscribe-bg;

		transition: transform 0.3s ease-out;
	}

	&--active {
		transform: translateX(0);
	}

	&__item {
		&:not(:last-child) {
			margin-right: 88px;

			@include bp(lg) {
				margin-right: 0;
				margin-bottom: 30px;
			}
		}

		&:nth-last-child(2) {
			margin-right: 36px;
		}
	}

	&__link {
		position: relative;

		font-family: 'Poppins';
		font-weight: 400;
		font-size: 16px;

		transition: opacity 0.3s;

		&:hover {
			opacity: 0.6;
		}

		&--active {
			&::after {
				position: absolute;
				bottom: -11px;
				left: -12px;
	
				width: 39px;
				height: 4px;
				
				@include bg;
				background-image: url('../images/sprite.svg#underline');
	
				content: '';
			}
		}


		&--contact {
			padding: 10px 28px;

			font-size: 13px;

			background-color: $orange;
			border-radius: 6px;
			transition: color 0.3s, background-color 0.3s;
			
			&::after {
				display: none;
			}
			
			&:hover {
				opacity: 1;
				background-color: $white;
				
				color: $black;
			}
		}
	}
}