.breadcrumbs {
	display: flex;
	justify-content: center;
	margin-bottom: 15px;

	list-style: none;

	&__item {
		&:not(:last-child) {
			position: relative;
			margin-right: 5px;
			padding-right: 15px;
		}

		&:not(:last-child)::after {
			position: absolute;
			right: 0;
			bottom: 2px;

			font-size: 12px;
			content: '>';
		}

		&:last-child {
			color: $breadcrumbs-color;
		}
	}

	&__link {
		font-size: 12px;
		line-height: 2;
	}
}